.skills_div {
  background-color: #9db1d6;
  min-width: 200px;
  max-width: 250px;
  width: 25%;
  height: 100%;
  padding-bottom: calc(100px - 3em);
  display: flex;
  flex-wrap: wrap;
  z-index: 2;
}

.skills_header_text {
  font-family: Georgia, 'Times New Roman', Times, serif;
  padding: 10px;
  margin: 0%;
}

.skills_subheader_text {
  text-align: left;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: large;
  font-weight: lighter;
  padding: 5px;
  margin-top: 0%;
}

.skills{
   padding: 5px;
   margin-top: 0%;
   margin-left: 50%;
}